<template>
<div class="sort_wrapper">
    <el-row :gutter="10">
        <el-col :span="20">
            <el-input v-model="sortValue.sortValue" :placeholder="$t('commodity.sortNamePlaceholder')" @input="handleInput"></el-input>
            <div class="err" v-show="errMsg">{{ errMsg }}</div>
        </el-col>
        <el-col :span="4">
            <el-button type="primary" :loading="isAdd" @click="addSort">{{
          $t('commodity.addTo')
        }}</el-button>
        </el-col>
    </el-row>
    <el-table :data="sortList" style="width: 100%" height="92%">
        <el-table-column :label="$t('commodity.sequence')" width="width">
            <template slot-scope="scope">
                {{ scope.$index + 1 }}
            </template>
        </el-table-column>
        <el-table-column prop="name" :label="$t('commodity.sortName')" width="width">
        </el-table-column>
        <el-table-column :label="$t('commodity.action')" width="width">
            <template slot-scope="scope">
                <el-button class="sort_btn" size="small" type="primary" :loading="loadingArr[scope.$index].moveup" @click="handleMoveup(scope)">{{ $t('btn.moveUp') }}</el-button>
                <el-button class="sort_btn" size="small" type="primary" plain :loading="loadingArr[scope.$index].delete" @click="handleDelete(scope)">{{ $t('btn.delete') }}</el-button>
            </template>
        </el-table-column>
    </el-table>
</div>
</template>

<script>
import {
    mapActions,
    mapMutations,
    mapState
} from 'vuex';
import Schema from 'async-validator';
export default {
    created() {
        this.loadingArr = this.getLoadingArray();
    },
    data() {
        return {
            sortValue: {
                sortValue: '',
            },
            isAdd: false,
            loadingArr: [],
            errMsg: '',

            rules: {
                sortValue: [{
                        required: true,
                        trigger: 'blur',
                        message: this.$t('commodity.categoryErrMsg'),
                    },
                    {
                        validator: (rule, value, callback) => {
                            let newVal = value.trim();
                            if (newVal.length < 3 || newVal.length > 20) {
                                callback(new Error(this.$t('commodity.sortLengthErrMsg')));
                            } else {
                                callback();
                            }
                        },
                    },
                    {
                        validator: (rule, value, callback) => {
                            let matchArray = this.sortList.filter(
                                (item) => item.name === value.trim()
                            );
                            if (matchArray.length > 0) {
                                callback(new Error(this.$t('error.sameNameErr')));
                            } else {
                                callback();
                            }
                        },
                    },
                ],
            },
        };
    },
    computed: {
        ...mapState({
            sortList: (state) => state.commodity.commodityCategorityList,
        }),
    },
    methods: {
        handleInput(value) {
            this.sortValue.sortValue = value;
            const validator = new Schema(this.rules);
            validator.validate(this.sortValue, (err, field) => {
                if (err && field.sortValue.length > 0) {
                    this.errMsg = field.sortValue[0].message;
                } else {
                    this.errMsg = null;
                }
            });
        },
        handleDelete(scope) {
            this.loadingArr.splice(scope.$index, 1, {
                moveup: false,
                delete: true,
            });
            this.deleteCommoditySort({
                    id: scope.row.id,
                })
                .then(() => {
                    this.loadingArr.splice(scope.$index, 1, {
                        moveup: false,
                        delete: false,
                    });
                })
                .catch(() => {
                    this.loadingArr.splice(scope.$index, 1, {
                        moveup: false,
                        delete: false,
                    });
                    this.$message.error(this.$t('error.serverErr'));
                });
        },
        handleMoveup(scope) {
            this.loadingArr.splice(scope.$index, 1, {
                moveup: true,
                delete: false,
            });
            this.moveupSortOrder(scope.row)
                .then(() => {
                    this.loadingArr.splice(scope.$index, 1, {
                        moveup: false,
                        delete: false,
                    });
                })
                .catch(() => {
                    this.$message.error(this.$t('error.serverErr'));
                    this.loadingArr.splice(scope.$index, 1, {
                        moveup: false,
                        delete: false,
                    });
                });
        },
        getLoadingArray() {
            let loadingArray = [];
            this.sortList.forEach(() => {
                loadingArray.push({
                    moveup: false,
                    delete: false,
                });
            });
            return loadingArray;
        },

        addSort() {
            const validator = new Schema(this.rules);
            validator.validate(this.sortValue, (err, field) => {
                if (err && field.sortValue.length > 0) {
                    this.errMsg = field.sortValue[0].message;
                } else {
                    this.errMsg = null;
                    this.isAdd = true;
                    let obj = {
                        name: this.sortValue.sortValue.trim(),
                        display_order: 1,
                        shop_id: this.$store.state.user.data.role_id,
                    };
                    this.addCommoditySort(obj).then(() => {
                        this.sortValue.sortValue = '';
                        this.isAdd = false;
                        this.loadingArr = this.getLoadingArray();
                    });
                }
            });
        },
        ...mapMutations({
            addCommodityCategoryList: 'commodity/ADD_COMMODITY_CATEGORITY_LIST',
            changeCommodityCategoryListSort: 'commodity/CHENGE_COMMODITY_CATEGORY_SORT',
        }),
        ...mapActions({
            addCommoditySort: 'commodity/addCommoditySort',
            deleteCommoditySort: 'commodity/deleteCommoditySort',
            moveupSortOrder: 'commodity/alterSortDisplayOrder',
        }),
    },
};
</script>

<style lang="scss" scoped>
.sort_wrapper {
    height: calc(100vh - 200px);
}

.sort_btn {
    width: 90px;
}

.err {
    color: #f56c6c;
    font-size: 12px;
    height: 12px;
    padding-left: 12px;
}
</style>
